/**
 * textarea
 * 
 * @memberof Util
 * @param {Object}
 *            elementID textareaID
 */
export default function(elementId) { // #61
    "usestrict";
    // textarea
    var area = document.getElementById(elementId);
    area.select();
    document.execCommand("copy");
}


